<script>
	export default {
		data() {
			return {
				bannerImage: '/static/banner/bj1.jpg',
				statusBarHeight: 0,
				currentBanner: 0,
				features: [
					{ key: 'intro', icon: 'üèõÔ∏è', text: 'ÂΩ±Âüé‰ªãÁªç' },
					{ key: 'vip', icon: 'üí≥', text: 'ÊÄé‰πà‰ºö' },
					{ key: 'contact', icon: '‚òéÔ∏è', text: 'ËÅîÁ≥ªÁîµËØù' }
				],
				banners: [],
				business: null,
				tags: ['4K HDR','ÊùúÊØîËßÜÁïå','Áã¨Á´ãËßÇÂΩ±','ÁßÅÂØÜÁ©∫Èó¥','Âç´Áîü‰øùÈöú'],
				hotRooms: [],
				themedRooms: [],
				loading: true,
				error: '',
				isOpenNow: false
			};
		},
			onLoad() {
			const info = uni.getSystemInfoSync();
			this.statusBarHeight = (info.statusBarHeight || 0);
			this.fetchHome();
		},
		methods: {
			isWithin(open, close) {
				// open/close: 'HH:mm'
				const pad = (n) => (n < 10 ? '0' + n : '' + n)
				const now = new Date()
				const cur = parseInt(pad(now.getHours()) + pad(now.getMinutes()))
				const o = parseInt((open || '10:00').replace(':',''))
				const c = parseInt((close || '24:00').replace(':',''))
				if (o <= c) return cur >= o && cur <= c
				// Ë∑®Â§©Ëê•‰∏öÔºåÂ¶Ç 22:00-02:00
				return cur >= o || cur <= c
			},
			async fetchHome() {
				this.loading = true;
				this.error = '';
				try {
					// ‰ªé‰∫ëÂáΩÊï∞Ëé∑ÂèñÈ¶ñÈ°µÊï∞ÊçÆ
					const res = await uniCloud.callFunction({
						name: 'cinema-home',
						data: {}
					});
					const data = res?.result?.data || {};
					this.hotRooms = data.hotRooms || this.mockRooms(6);
					this.themedRooms = data.themedRooms || this.mockRooms(6);
					this.banners = data.banners || [];
					this.business = data.business || null;
					this.isOpenNow = this.isWithin(this.business.openTime, this.business.closeTime)
				} catch (e) {
					this.error = 'Âä†ËΩΩÂ§±Ë¥•ÔºåÂ∑≤‰∏∫ÊÇ®Â±ïÁ§∫Á§∫‰æãÊï∞ÊçÆ';
					this.hotRooms = this.mockRooms(6);
					this.themedRooms = this.mockRooms(6);
				} finally {
					this.loading = false;
				}
			},
			mockRooms(n) {
				const arr = [];
				for (let i = 0; i < n; i++) {
					arr.push({
						id: 'm' + i,
						name: ['ÊÉÖ‰æ£‰∏ªÈ¢òÊàø3','ÂÆ∂Â∫≠Ê¨¢ËÅöÊàø','Êº´Â®Å‰∏ªÈ¢òÊàø','ÁîµÁ´ûËßÇËµõÊàø','Â•≥Á•û‰∏ì‰∫´Êàø','Â§çÂè§ËÉ∂ÁâáÊàø'][i % 6],
						capacity: [2,4,6,4,2,6][i % 6],
						cover: '/static/uni-center/headers.png'
					});
				}
				return arr;
			},
			onFeatureClick(f) {
				// È¢ÑÁïôË∑ØÁî±
				uni.showToast({ title: f.text, icon: 'none' });
			},
			toMore(key) {
				uni.navigateTo({ url: '/pages/list/list.nvue?type=' + key });
			},
			toDetail(room) {
				uni.navigateTo({ url: '/pages/list/detail?roomId=' + room.id });
			},
			onBannerChange(e){
				this.currentBanner = e?.detail?.current || 0;
			}
		}
	}
</script>

